html {font-size:$base-font-size;}
body {font-family:$base-font-family;background-color:#f9f9fc;font-weight:400;font-size:$base-font-size;}

a {
    color: $color-primary;

    &:hover,
    &:focus {
        color: map-get($colors-darkest, 'primary');
    }
}

::selection {background:$color-primary;color:#fff;}
::-moz-selection {background:$color-primary;color:#fff;}

.btn {
    font-size: $btn-font-size;
    border-radius: $btn-border-radius;
    padding: 0 $btn-padding-side;
    height: $btn-height;
    line-height: $btn-height - ($btn-border-size * 2);
}
.btn.btn-lg {
    font-size: 16px;
    padding: 0 20px;
    height: 44px;
    line-height: 42px;
}
.btn.btn-sm {
    font-size: 11px;
    padding: 0 18px;
    height: 26px;
    line-height: 24px;
}

@each $type in $list-base-colors {
    /**
     * Default buttons.
     */
    .btn-#{$type} {
        background-color: map-get($colors, $type);
        border-color: map-get($colors, $type);
        @include btn-drop-shadow(map-get($colors, $type));

        &:focus,
        &:hover,
        &:active {
            background-color: map-get($colors-darken, $type);
            border-color: map-get($colors-darken, $type);
        }

        &:focus,
        &.focus {
            color: #fff;
            box-shadow: 0 0 0 0.2rem rgba(map-get($colors-darken, $type), .5);
        }

        &.disabled,
        &:disabled {
            background-color: map-get($colors, $type) !important;
            border-color: map-get($colors, $type) !important;
            cursor: auto !important;
        }
    }

    .btn-#{$type}:not(:disabled):not(.disabled).active,
    .btn-#{$type}:not(:disabled):not(.disabled):active,
    .show > .btn-#{$type}.dropdown-toggle {
        background-color: map-get($colors-darkest, $type);
        border-color: map-get($colors-darkest, $type);
    }

    .btn-#{$type}:not(:disabled):not(.disabled).active:focus,
    .btn-#{$type}:not(:disabled):not(.disabled):active:focus,
    .show > .btn-#{$type}.dropdown-toggle:focus {
        box-shadow: 0 0 0 0.2rem rgba(map-get($colors-darkest, $type), 0.5);
    }


    /**
     * Outline buttons
     */
    .btn-outline-#{$type} {
        color: map-get($colors, $type);
        border-color: map-get($colors-lighten, $type);
        @include btn-drop-shadow(map-get($colors, $type));

        &:focus,
        &:hover {
            background-color: map-get($colors-darken, $type);
            border-color: map-get($colors-darken, $type);
            color: #fff;
        }
    }

    .btn-outline-#{$type}:not(:disabled):not(.disabled).active,
    .btn-outline-#{$type}:not(:disabled):not(.disabled):active,
    .show>.btn-outline-#{$type}.dropdown-toggle{
        background-color: map-get($colors-darkest, $type);
        border-color: map-get($colors-darkest, $type);
    }
}

/**
 * Specific styles
 */
.btn-outline-light {
    color: #777;
    &:focus,
    &:hover {
        color: #000;
    }
}



.btn.btn-icon-only,
.btn.btn-icon-left,
.btn.btn-icon-right {
    position: relative;
}
.btn.btn-icon-only .btn-icon,
.btn.btn-icon-left .btn-icon,
.btn.btn-icon-right .btn-icon {
    font-size: 1rem;
    height: 1rem;
    line-height: 1rem;
    text-align: center;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}

.btn.btn-icon-left {
    @include btn-icon-left-padding;

    .btn-icon {
        left: 15px;
    }
    &.btn-lg {
        padding-left: 50px;

        .btn-icon {
            left: 17px;
            font-size: 1.1rem;
        }
    }
    &.btn-sm {
        padding-left: 36px;
        padding-right: 15px;

        .btn-icon {
            left: 12px;
            font-size: 0.8rem;
        }
    }
}

.btn.btn-icon-right {
    @include btn-icon-right-padding;

    .btn-icon {right: 15px;}
    &.btn-lg {
        padding-right: 50px;

        .btn-icon {
            right: 17px;
            font-size: 1.1rem;
        }
    }
    &.btn-sm {
        padding-right: 36px;
        padding-left: 15px;

        .btn-icon {
            right: 12px;
            font-size: 0.8rem;
        }
    }
}

.btn.btn-icon-only {
    @include btn-icon-only-padding;

    .btn-icon {
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }
    &.btn-lg {
        padding-left: 26px;
        padding-right: 26px;
        font-size: 1.1rem;

        .btn-icon {
            font-size: 1.2rem;
        }
    }
    &.btn-sm {
        padding-left: 18px;
        padding-right: 18px;

        .btn-icon {
            font-size: 1rem;
        }
    }
}

.dropdown-toggle {
    padding: 0 17px;
}
.dropdown-toggle::after {
    margin-left: .95rem;
    border-top: .4em solid;
    border-right: .4em solid transparent;
    border-left: .4em solid transparent;
    transform: translateY(1px);
}
.dropdown-toggle.dropdown-toggle-split {
    border-left: 1px solid #fff
}
.dropup .dropdown-toggle::after {
    margin-left: .95rem;
    border-right: .4em solid transparent;
    border-bottom: .4em solid;
    border-left: .4em solid transparent;
}

.dropdown-menu {
    padding: 8px 0;
    min-width: 170px;
    border: none;
    box-shadow: $box-shadow;

    &.show {
        animation-name: zoomInDropdown;
        animation-iteration-count: 1;
        animation-duration: .12s;
    }
}

.dropdown-header {
    font-size: 12px;
    padding: $dropdown-item-padding;
    text-transform: uppercase;
}

@keyframes zoomInDropdown {
    0% {opacity:0;}
    100% {opacity:1;}
}

.dropdown-item {
    padding: $dropdown-item-padding;
    font-size: 13px;
    line-height: 1.6;
    transition: $transition-time all;
    position: relative;
    color: $dropdown-item-color;

    &.active {
        padding-right: 35px;
        background: rgba(0,0,0,.06);
        color: inherit;
    }

    &.active:after {
        content: "\f00c";
        font-family: "Font Awesome 5 Free";
        font-weight: bold;
        display: block;
        position: absolute;
        top: 50%;
        right: 12px;
        font-size: 13px;
        color: #9aa0ac;
        transform: translateY(-50%);
    }

    &:hover,
    &:focus {
        background-color: map-get($colors, 'primary');
        color: #fff;
    }

    &.active:hover:after,
    &.active:focus:after {
        color: #fff;
    }

    @each $type in $list-base-colors {
        &.dropdown-item-#{$type}:hover,
        &.dropdown-item-#{$type}:focus {
            background-color: map-get($colors, $type);
        }
    }

    &.dropdown-item-light:hover,
    &.dropdown-item-light:focus {
        background-color: map-get($colors-darkest, 'light');
        color: #000;
    }

    &.dropdown-item-with-icon {
        padding-left: 40px;

        .dropdown-icon {
            transition: $transition-time all;
            display: block;
            position: absolute;
            left: 15px;
            top: 50%;
            transform: translateY(-50%);
            width: 18px;
            height: 13px;
            line-height: 13px;
            font-size: 13px;
            text-align: center;
            color: #9aa0ac;
        }

        @each $type in $list-base-colors {
            &.dropdown-item-#{$type} .dropdown-icon {
                //color: map-get($colors, $type);
            }
        }

        &.dropdown-item-light .dropdown-icon {
            color: invert(map-get($colors, 'light'));
        }

        &:hover,
        &:focus {
            .dropdown-icon {
                color: #fff;
            }
        }

        &.dropdown-item-light:hover .dropdown-icon,
        &.dropdown-item-light:focus .dropdown-icon {
            color: #000;
        }
    }
}

.dropdown-divider {margin:8px 0;}

.form-group label {font-size:13px;margin-bottom:5px;display:inline-block;}
.form-group.form-group-has-info label {position:relative;padding-right:18px}
.form-group.form-group-has-info label .form-control-info {opacity:0;position:absolute;right:0;top:50%;transform:translateY(-50%);width:12px;height:12px;line-height:1;color:#444;transition:.12s all}
.form-group.form-group-has-info label .form-control-info:before {position:absolute;display:block;line-height:12px;font-size:12px;content:"\f05a";font-family:"Font Awesome 5 Free";font-weight:bold;}
.form-group.form-group-has-info label .form-control-info:hover {cursor:pointer;color:$color-primary;}
.form-group.form-group-has-info label:hover .form-control-info {opacity:1}

.form-control {
    font-size: $form-control-font-size;
    height: $form-control-height;
    line-height: $form-control-height - ($form-control-border-size * 2);
    padding: 0 $form-control-padding-side;
    border: 1px solid $form-control-border-color;
    border-radius: $form-control-border-radius;
    transition: $form-control-transition;

    &:focus {
        border-color: $form-control-focus-border-color;
        box-shadow: $form-control-focus-box-shadow;
    }
}
.form-control.form-control-lg {font-size:1rem;height:40px;line-height:40px;}
.form-control.form-control-sm {font-size:.6rem;height:18px;line-height:18px;}

textarea.form-control {
    line-height: 1.2;
    min-height: 70px;
    padding: $form-control-padding-side;
}

.input-group {
    .btn {
        padding: 0 $btn-input-group-padding-size;

        &.btn-default {
            border: 1px solid $form-control-border-color;

            &:hover,
            &:focus {
                background-color: $form-control-border-color;
            }
        }

        &.btn-icon-only {
            padding-left: 18px;
            padding-right: 18px;
        }
    }

    .btn.btn-icon-left {
        @include btn-icon-left-padding;
    }
    .btn.btn-icon-right {
        @include btn-icon-right-padding;
    }
    .btn.btn-icon-only {
        @include btn-icon-only-padding;
    }
}

.input-group-text {
    color: $form-control-addon-color;
    background-color: $form-control-addon-background;
    height: $form-control-height;
    line-height: $form-control-height - ($form-control-border-size * 2);
    padding: 0 $form-control-addon-padding-side;
    border: 1px solid $form-control-border-color;
    border-radius: $form-control-border-radius;
    min-width: 36px;
    text-align: center;
}

.custom-control {
    font-size: 13px;
    min-height: 24px;
    padding-left: 20px;
}

.custom-control-label::before {
    left: -20px;
    width: 13px;
    height: 13px;
}

.custom-control-label::after {
    left: -20px;
    width: 13px;
    height: 13px;
    background-position: center center;
    background-size: 69%;
    background-repeat: no-repeat;
}

.custom-control-label:hover {
    cursor: pointer;
}

.custom-control-label:hover::before {
    border-color: #555;
}

.custom-switch {
    padding-left: 30px;
}

.custom-control-input:checked ~ .custom-control-label::before {
    border-color: $color-primary;
    background-color: $color-primary;
}

