.input-group-text {
    font-size: 1.1rem;
}


.toast {background-color:#fff;}

.page-link {padding:8px 9px;font-size:12px;color:$color-primary;text-align:center;border-color:#e6e6e6}
.page-link:hover,
.page-link:focus {border-color:#c2cfd5;}
.page-item.active .page-link,
.page-item.active .page-link:focus,
.page-item.active .page-link:hover {background-color:$color-primary;border-color:$color-primary;}
.page-item > .page-link {margin:0;border-right-width:0;min-width:27px;}
.page-item:last-child > .page-link {border-right-width:1px;}

.badge-primary {background-color:$color-primary;}

.form-text {line-height: 1.2;font-size: 11px;}


@each $type in $list-base-colors {
    .alert-#{$type} {
        background-color:lighten(map-get($colors-lightest, $type), $alert-bg-transparentness);
        border-color:lighten(map-get($colors, $type), $alert-bg-transparentness);
    }
}

@each $type in $list-base-colors {
    .badge-#{$type} {
        background-color:map-get($colors, $type);
    }
}

@each $type in $list-base-colors {
    .bg-#{$type} {
        background-color:map-get($colors, $type) !important;
    }
}

@each $type in $list-base-colors {
    .text-#{$type} {
        color:map-get($colors, $type) !important;
    }
}


.modal-header .close {
    padding: 1.2rem;
}

.nav-tabs {
    margin-bottom: 15px;
    border-bottom: 1px solid $border-color;

    .nav-link {
        padding: 10px 20px;
        border-top-left-radius: $border-radius;
        border-top-right-radius: $border-radius;
        border: none;
        border-bottom: 3px solid transparent;
        transition: $transition-time all;
    }
}

.nav-tabs .nav-item.show .nav-link,
.nav-tabs .nav-link.active {
    background-color: rgba(map-get($colors, 'primary'), .11);
    border-bottom-color: rgba(map-get($colors, 'primary'), .6);
    color: #000;
}


.list-group-item {
    color: $dropdown-item-color;
    transition: $transition-time all;

    &:hover {
        text-decoration: none;
        background-color: rgba(map-get($colors, 'primary'), .11);
        color: $color-text;
    }
    &.active {
        background-color: map-get($colors-lightest, 'primary');
        border-color: map-get($colors-lightest, 'primary');
        color: #fff;
    }

    &.list-group-item-with-icon {
        position: relative;
        padding-left: 37px;

        .list-group-item-icon {
            display: block;
            position: absolute;
            left: 11px;
            top: 50%;
            transform: translateY(-50%);
            width: 14px;
            height: 14px;
            line-height: 14px;
            font-size: 14px;
            text-align: center;
            color: #9aa0ac;
            transition: $transition-time all;
        }
    }

    &.list-group-item-with-icon:hover {
        .list-group-item-icon {
            color: $color-text;
        }
    }

    &.list-group-item-with-icon.active {
        .list-group-item-icon {
            color: #fff;
        }
    }
}

.empty-form-section-placeholder:empty:before {
    content: attr(data-placeholder);
    font-size: 14px;
    opacity: .6;
    text-align: left;
    text-transform: uppercase;
}
